@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: local("Open Sans"), local("OpenSans-Regular"),
    url("../fonts/OpenSans/OpenSans-Regular.woff2") format("woff2"),
    url("../fonts/OpenSans/OpenSans-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: local("Open Sans Bold"), local("OpenSans-Bold"),
    url("../fonts/OpenSans/OpenSans-Bold.woff2") format("woff2"),
    url("../fonts/OpenSans/OpenSans-Bold.ttf") format("truetype");
}

@font-face {
  font-family: "Phetsarath OT";
  src: url("../fonts/Phetsarath/phetsarath_ot.eot");
  src: url("../fonts/Phetsarath/phetsarath_ot.eot?#iefix")
      format("embedded-opentype"),
    url("../fonts/Phetsarath/phetsarath_ot.woff2") format("woff2"),
    url("../fonts/Phetsarath/phetsarath_ot.woff") format("woff"),
    url("../fonts/Phetsarath/phetsarath_ot.ttf") format("truetype"),
    url("../fonts/Phetsarath/phetsarath_ot.svg#Phetsarath_OT") format("svg");
  font-weight: normal;
  font-style: normal;
}
