// Import global vars
@import "master";
@import "fonts";

html,
body {
  height: 100%;
  padding: 0;
}
body {
  display: flex;
  flex-direction: column;
  font-size: 16px;
  font-family: "Roboto", sans-serif;
  color: #000;

  // &.shrinked {
  //   transform: scale(0.85);
  // }
}
body.lang-la {
  font-family: 'Phetsarath OT', sans-serif;
}

.main-content {
  flex: 1 0 auto;
}
.main-footer {
  flex-shrink: 0;
}

img {
  max-width: 100%;
}

a {
  color: #da291c;
  transition: 200ms;
  &:hover {
    color: darken(#da291c, 5%);
  }
}

.ty-button {
  border: none;
  outline: none;

  font-weight: bold;
  font-size: 26px;
  color: #fff;

  border-radius: 44px;
  background: #da291c;

  padding: 16px 34px;
  min-width: 220px;

  display: inline-block;
  text-align: center;
  text-decoration: none !important;
  color: #fff !important;

  transition: 200ms;
  &:hover {
    background: darken(#da291c, 5%);
  }

  &--grey {
    background: #d0d0d0;
    &:hover {
      background: darken(#d0d0d0, 5%);
    }
  }
  &--slim {
    font-size: 20px;
    padding: 10px 18px;
  }
}

.ty-custom-select-wrap {
  width: 280px;
  position: relative;
}
.ty-custom-select {
  padding: 22px 40px;
  height: auto;
  width: 100%;
  border-radius: 44px;
  background: #fff;
  font-weight: 500;
  font-size: 30px;
  color: #da291c;

  outline: none !important;
  box-shadow: none !important;
  border: none !important;
  background: #fafafa;

  padding-left: 84px;
  padding-right: 60px;

  &__icon-pre {
    position: absolute;
    top: 50%;
    left: 26px;
    transform: translateY(-50%);

    font-size: 40px;
    color: #da291c;
    pointer-events: none;
    user-select: none;
  }
  &__icon-after {
    position: absolute;
    top: 50%;
    right: 26px;
    transform: translateY(-50%);

    font-size: 13px;
    color: #a3a3a3;
    pointer-events: none;
    user-select: none;
  }
}

.main-header {
  padding-top: 20px;
  padding-bottom: 30px;

  &__logo {
    max-width: 210px;
  }
  &__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__language {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    &-item {
      a {
        text-decoration: none !important;
      }

      font-weight: bold;
      opacity: 0.8;
      &:not(:last-child) {
        margin-right: 15px;
      }

      transition: 200ms;
      &:hover,
      &.active {
        opacity: 1;
      }
    }
  }
}
.main-content {
  padding-bottom: 30px;
}

.start-screen {
  &__title {
    font-weight: bold;
    font-size: 50px;
    color: #da291c;

    margin-top: 120px;
    margin-bottom: 20px;
  }
  &__content {
    font-weight: 300;
    font-size: 21px;
    line-height: 28px;
    margin-bottom: 40px;
  }
  &__controls {
    margin-bottom: 60px;
    display: flex;
    flex-direction: column;
    // flex-wrap: wrap;
    // align-items: center;
  }
  &__control-item {
    margin-bottom: 15px;
    &:not(:last-child) {
      margin-right: 20px;
    }
  }

  &__splash-wrap {
    margin-top: 30px;
  }

  &__footer {
    font-weight: 300;
    font-size: 18px;
    line-height: 22px;
    // color: #da291c;
  }
  &__legal-menu {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    &-item {
      position: relative;
      font-weight: bold;
      &:not(:last-child) {
        margin-right: 12px;
        &::after {
          content: "|";
          position: absolute;
          color: #000;
          top: 0;
          right: -8px;
        }
      }
    }
  }
}

.stepper {
  &__header {
    text-align: center;
    margin-bottom: 20px;
  }
  &__title {
    font-weight: bold;
    font-size: 30px;
    color: #da291c;
  }

  &__bullets {
    user-select: none;
    pointer-events: none;

    margin-top: auto;
    display: flex;
    justify-content: space-between;

    width: 500px;
    margin: auto;

    margin-bottom: 20px;

    &-item {
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
      flex: 1;

      &::before {
        position: absolute;
        content: "";
        border-bottom: 10px solid #d0d0d0;
        width: 100%;
        top: 20px;
        left: -50%;
        z-index: 2;
      }
      &::after {
        position: absolute;
        content: "";
        border-bottom: 10px solid #d0d0d0;
        width: 100%;
        top: 20px;
        left: 50%;
        z-index: 2;
      }

      &-counter {
        position: relative;
        z-index: 5;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        background: #d0d0d0;
        margin-bottom: 6px;

        color: #fff;
        width: 50px;
        height: 50px;
        font-size: 34px;
        font-weight: bold;
        border: 2px solid #fff;
      }

      &.completed {
        .stepper__bullets-item-counter {
          background-color: #da291c;
        }
        &::after {
          border-color: #da291c;
          z-index: 3;
        }
      }
      &.active {
        &::before {
          z-index: 3;
        }
      }

      &:first-child::before {
        content: none;
      }
      &:last-child::after {
        content: none;
      }
    }
  }

  &__content {
    margin-bottom: 30px;
  }
  &__footer {
    text-align: center;

    & > * {
      &:not(:last-child) {
        margin-right: 15px;
      }
    }
  }
}

.field-wrapper {
  display: flex;
  align-items: center;

  margin-bottom: 15px;

  &--vertical {
    flex-direction: column;
    align-items: flex-start;

    .field-label {
      width: auto;
    }
  }
}
.field-label-wrap {
  width: 300px;
  position: relative;
  min-height: 45px;

  display: flex;
  // flex-direction: column;
  align-items: center;
  flex: 0 0 auto;
  margin-right: 20px;
}
.field-label {
  display: inline-flex;
  align-items: center;
  flex: 0 0 auto;

  font-weight: normal;
  font-size: 18px;
  color: #505050;

  margin-bottom: 0;

  &__icon {
    font-size: 30px;
    color: #da291c;
    margin-right: 20px;
  }

  &--column {
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
  }

  &--required {
    position: relative;
    &::after {
      content: "*";
      position: absolute;
      top: 0;
      right: -10px;
      font-size: 10px;
      color: red;
    }
