  background: #fafafa;
  border: 1px solid #e8e8e8;

  padding: 6px 15px;
  width: 100%;
  outline: none;
}
.view-chart-btn {
  font-size: 38px;
  color: #da291c;

  background: none;
  border: none;
  outline: none;
  padding: 0;

  margin-left: auto;
}

.field-double-container {
  display: flex;
  align-items: center;
  width: 100%;
}
.field-double {
  &__item {
    &:first-child {
      flex: 0 0 auto;
    }
    &:not(:last-child) {
