.overlay-cdk {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(#000, 0.3);
  z-index: 9999;

  display: none;
  &.active {
    display: block;
  }

  &__content-wrap {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    max-width: 650px;
    width: 100%;
    padding: 0 10px;
  }
  &__content {
    position: relative;
    width: 100%;
    border-radius: 10px;
    background: #fff;
    box-shadow: 0px 0px 7px rgba(0, 0, 0, 0.16);
    padding: 40px;
    text-align: center;

    &-chart {
      padding: 60px;
    }
    &-title {
      font-weight: bold;
      font-size: 32px;
      line-height: 1em;
      text-align: center;
      color: #da291c;
      max-width: 390px;
      margin: auto;

      &.result {
        display: none;
        &.active {
          display: inline-block;
        }

        .value {
          display: none;
          &.active {
            display: block;
          }
        }
        .value-fallback {
          display: none;
          &.active {
            display: block;
          }
        }
      }
    }
  }
  &__close-btn {
    font-size: 32px;
    padding: 12px;
    border-radius: 50%;
    background: #da291c;
    border: none;
    outline: none;
    color: #fff;
    position: absolute;
    top: -30px;
    right: 40px;
    z-index: 1;
    i {
      display: flex;
    }
  }
}

.input-error {
  color: red;
  span,
  label {
    color: red;
  }

  .input-error-text {
    display: block;
  }
}
.input-error-text {
